<template>

 <article class="block_half color__default">
                        <form action="" v-on:submit.prevent="send">
                            <div class="form-group">
                                <label class="control-label sr-only">Name</label>
                                <input type="text" class="form-control field_rounded control_dark" placeholder="Your Full name" v-model="fullname">
                            </div>
                            <div class="form-group">
                                <label class="control-label sr-only">Email Address/Phone Number</label>
                                <input type="text" class="form-control field_rounded control_dark" placeholder="Email Address/Phone Number" v-model="email">
                            </div>
                            <div class="form-group">
                                <label class="control-label sr-only">Subject (Why you reaching out)</label>
                                <input type="text" class="form-control field_rounded control_dark" placeholder="Subject (Why you reaching out)" v-model="subject">
                            </div>
                            <div class="form-group">
                                <label class="control-label sr-only">Message</label>
                                <textarea rows="10" class="form-control field_rounded control_dark" placeholder="Your message here" v-model="message"></textarea>
                            </div>
                            <div class="action_btn text-right">
                                <button data-target="#submissionFeedback" type="Submit" data-toggle="modal" class="btn btn-secondary" v-bind:class="{btn:true,'btn-secondary':true,disabled:!isEnabled}">
                                    Submit
                                </button>
                            </div>
                        </form>
     </article>

</template>

<script>

 import {AxiosInit} from "../assets/js/utility/AsyncRequest"

var componetInstance;
  export default{
     data (){
         return {
             fullname:"",
             email:"",
             subject:"",
             message:"",
             isEnabled:true
         }
     },
     methods:{
         send:function(event){
            
            if(this.isEnabled){
            componetInstance = this;
            var axiosInstance = AxiosInit(); 
            this.isEnabled = false;
            var url = "contact_us";
          // var url = "http://quizacweb.azurewebsites.net/contact_us";

            var data = {
                fullname:this.fullname,
                email:this.email,
                subject:this.subject,
                message:this.message,
            };

            axiosInstance.post(url,data).then(function(response){   
                componetInstance.fullname="";
                componetInstance.email ="";
                componetInstance.subject ="";
                componetInstance.message="";
                componetInstance.isEnabled=true; 
            }).catch(function (error) {
               // console.log("error log", error);
                componetInstance.isEnabled=true; 
            })  
            
            }
         }
     }
 }

</script>




