var axios = require('axios');

export const AxiosInit = (authentication_token)=>{

    /* set authentication header here also */

    var config = {
        baseURL: "http://quizacweb.azurewebsites.net/",
        transformRequest: [function (data, headers) {
            // Do whatever you want to transform the data
        
         console.log("Request Data", data);
         console.log("Request header", headers);
            return data;
          }]
    }
    var axiosInstance = axios.create(config);

    //axiosInstance.defaults.headers.common['Content-Type'] ="application/json";
    axiosInstance.defaults.headers.post['Content-Type'] ="application/json";
    axiosInstance.defaults.headers.post['Cache-Control'] = "no-cache";
    
    //axiosInstance.defaults.headers.patch['Content-Type'] ="application/json";
    //axiosInstance.defaults.headers.put['Content-Type'] ="application/json";

  /*  if(authentication_token != null && authentication_token != undefined){
        axiosInstance.defaults.headers.common["Authorization"] = authentication_token;
        
    }*/

    return axiosInstance;
} 


